"use client"

import type React from "react"

import { useState, useCallback } from "react"
import useSWR, { mutate } from "swr"
import { toast } from "sonner"
import {
  Folder,
  File,
  Upload,
  FolderPlus,
  Download,
  Trash2,
  ChevronRight,
  Home,
  FileArchive,
  Loader2,
} from "lucide-react"
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog"
import { cn } from "@/lib/utils"

interface FileItem {
  id: string
  name: string
  originalName: string
  size: number
  compressedSize: number
  type: string
  folderId: string | null
  createdAt: string
}

interface FolderItem {
  id: string
  name: string
  parentId: string | null
  createdAt: string
}

const fetcher = (url: string) => fetch(url).then((res) => res.json())

function formatBytes(bytes: number): string {
  if (bytes === 0) return "0 B"
  const k = 1024
  const sizes = ["B", "KB", "MB", "GB"]
  const i = Math.floor(Math.log(bytes) / Math.log(k))
  return Number.parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + " " + sizes[i]
}

function getCompressionRatio(original: number, compressed: number): string {
  if (original === 0) return "0%"
  const ratio = ((1 - compressed / original) * 100).toFixed(1)
  return `${ratio}% saved`
}

function getFileIcon(type: string) {
  if (type.startsWith("image/")) return "üñºÔ∏è"
  if (type.startsWith("video/")) return "üé¨"
  if (type.startsWith("audio/")) return "üéµ"
  if (type.includes("pdf")) return "üìÑ"
  if (type.includes("zip") || type.includes("rar") || type.includes("7z")) return "üì¶"
  if (type.includes("text") || type.includes("document")) return "üìù"
  return "üìé"
}

export function FileExplorer() {
  const [currentFolderId, setCurrentFolderId] = useState<string | null>(null)
  const [isUploading, setIsUploading] = useState(false)
  const [newFolderName, setNewFolderName] = useState("")
  const [folderDialogOpen, setFolderDialogOpen] = useState(false)
  const [dragActive, setDragActive] = useState(false)

  const { data: files = [], isLoading: filesLoading } = useSWR<FileItem[]>(
    `/api/files?folderId=${currentFolderId || ""}`,
    fetcher,
  )

  const { data: folders = [], isLoading: foldersLoading } = useSWR<FolderItem[]>(
    `/api/folders?parentId=${currentFolderId || ""}`,
    fetcher,
  )

  const { data: breadcrumbs = [] } = useSWR<FolderItem[]>(
    currentFolderId ? `/api/folders?breadcrumbs=${currentFolderId}` : null,
    fetcher,
  )

  const refreshData = useCallback(() => {
    mutate(`/api/files?folderId=${currentFolderId || ""}`)
    mutate(`/api/folders?parentId=${currentFolderId || ""}`)
  }, [currentFolderId])

  const handleFileUpload = async (fileList: FileList | null) => {
    if (!fileList || fileList.length === 0) return

    setIsUploading(true)

    for (const file of Array.from(fileList)) {
      const formData = new FormData()
      formData.append("file", file)
      if (currentFolderId) {
        formData.append("folderId", currentFolderId)
      }

      try {
        const response = await fetch("/api/files", {
          method: "POST",
          body: formData,
        })

        if (!response.ok) throw new Error("Upload failed")

        const uploadedFile = await response.json()
        toast.success(`Uploaded ${file.name}`, {
          description: `Compressed: ${getCompressionRatio(uploadedFile.size, uploadedFile.compressedSize)}`,
        })
      } catch {
        toast.error(`Failed to upload ${file.name}`)
      }
    }

    setIsUploading(false)
    refreshData()
  }

  const handleCreateFolder = async () => {
    if (!newFolderName.trim()) return

    try {
      const response = await fetch("/api/folders", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          name: newFolderName.trim(),
          parentId: currentFolderId,
        }),
      })

      if (!response.ok) throw new Error("Failed to create folder")

      toast.success(`Created folder: ${newFolderName}`)
      setNewFolderName("")
      setFolderDialogOpen(false)
      refreshData()
    } catch {
      toast.error("Failed to create folder")
    }
  }

  const handleDownload = async (file: FileItem) => {
    try {
      const response = await fetch(`/api/files/${file.id}`)
      if (!response.ok) throw new Error("Download failed")

      const blob = await response.blob()
      const url = URL.createObjectURL(blob)
      const a = document.createElement("a")
      a.href = url
      a.download = file.originalName
      document.body.appendChild(a)
      a.click()
      document.body.removeChild(a)
      URL.revokeObjectURL(url)

      toast.success(`Downloaded ${file.originalName}`)
    } catch {
      toast.error("Failed to download file")
    }
  }

  const handleDeleteFile = async (file: FileItem) => {
    try {
      const response = await fetch(`/api/files/${file.id}`, { method: "DELETE" })
      if (!response.ok) throw new Error("Delete failed")

      toast.success(`Deleted ${file.originalName}`)
      refreshData()
    } catch {
      toast.error("Failed to delete file")
    }
  }

  const handleDeleteFolder = async (folder: FolderItem) => {
    try {
      const response = await fetch(`/api/folders/${folder.id}`, { method: "DELETE" })
      if (!response.ok) throw new Error("Delete failed")

      toast.success(`Deleted folder: ${folder.name}`)
      refreshData()
    } catch {
      toast.error("Failed to delete folder")
    }
  }

  const handleDrag = (e: React.DragEvent) => {
    e.preventDefault()
    e.stopPropagation()
    if (e.type === "dragenter" || e.type === "dragover") {
      setDragActive(true)
    } else if (e.type === "dragleave") {
      setDragActive(false)
    }
  }

  const handleDrop = (e: React.DragEvent) => {
    e.preventDefault()
    e.stopPropagation()
    setDragActive(false)
    handleFileUpload(e.dataTransfer.files)
  }

  const isLoading = filesLoading || foldersLoading

  return (
    <div className="min-h-screen bg-background">
      {/* Header */}
      <header className="border-b border-border bg-card/50 backdrop-blur-sm sticky top-0 z-10">
        <div className="container mx-auto px-4 py-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-3">
              <div className="flex items-center justify-center w-10 h-10 rounded-lg bg-primary/10">
                <FileArchive className="w-5 h-5 text-primary" />
              </div>
              <div>
                <h1 className="text-xl font-semibold text-foreground">LanDrop</h1>
                <p className="text-xs text-muted-foreground">Local Network File Sharing</p>
              </div>
            </div>

            <div className="flex items-center gap-2">
              <Dialog open={folderDialogOpen} onOpenChange={setFolderDialogOpen}>
                <DialogTrigger asChild>
                  <Button variant="outline" size="sm" className="gap-2 bg-transparent">
                    <FolderPlus className="w-4 h-4" />
                    <span className="hidden sm:inline">New Folder</span>
                  </Button>
                </DialogTrigger>
                <DialogContent>
                  <DialogHeader>
                    <DialogTitle>Create New Folder</DialogTitle>
                  </DialogHeader>
                  <div className="flex gap-2 mt-4">
                    <Input
                      placeholder="Folder name"
                      value={newFolderName}
                      onChange={(e) => setNewFolderName(e.target.value)}
                      onKeyDown={(e) => e.key === "Enter" && handleCreateFolder()}
                    />
                    <Button onClick={handleCreateFolder}>Create</Button>
                  </div>
                </DialogContent>
              </Dialog>

              <label>
                <Button size="sm" className="gap-2 cursor-pointer" disabled={isUploading}>
                  {isUploading ? <Loader2 className="w-4 h-4 animate-spin" /> : <Upload className="w-4 h-4" />}
                  <span className="hidden sm:inline">Upload</span>
                </Button>
                <input
                  type="file"
                  multiple
                  className="hidden"
                  onChange={(e) => handleFileUpload(e.target.files)}
                  disabled={isUploading}
                />
              </label>
            </div>
          </div>

          {/* Breadcrumbs */}
          <nav className="flex items-center gap-1 mt-4 text-sm">
            <Button variant="ghost" size="sm" className="h-7 px-2 gap-1" onClick={() => setCurrentFolderId(null)}>
              <Home className="w-4 h-4" />
              <span>Home</span>
            </Button>
            {breadcrumbs.map((crumb) => (
              <div key={crumb.id} className="flex items-center">
                <ChevronRight className="w-4 h-4 text-muted-foreground" />
                <Button variant="ghost" size="sm" className="h-7 px-2" onClick={() => setCurrentFolderId(crumb.id)}>
                  {crumb.name}
                </Button>
              </div>
            ))}
          </nav>
        </div>
      </header>

      {/* Main Content */}
      <main className="container mx-auto px-4 py-6">
        {/* Drop Zone */}
        <div
          className={cn(
            "border-2 border-dashed rounded-xl p-8 mb-6 transition-all duration-200",
            dragActive ? "border-primary bg-primary/5" : "border-border hover:border-primary/50",
          )}
          onDragEnter={handleDrag}
          onDragLeave={handleDrag}
          onDragOver={handleDrag}
          onDrop={handleDrop}
        >
          <div className="text-center">
            <Upload
              className={cn(
                "w-10 h-10 mx-auto mb-3 transition-colors",
                dragActive ? "text-primary" : "text-muted-foreground",
              )}
            />
            <p className="text-sm text-muted-foreground">Drag and drop files here, or click Upload</p>
            <p className="text-xs text-muted-foreground mt-1">
              Files are automatically compressed for efficient storage
            </p>
          </div>
        </div>

        {isLoading ? (
          <div className="flex items-center justify-center py-12">
            <Loader2 className="w-8 h-8 animate-spin text-muted-foreground" />
          </div>
        ) : (
          <>
            {/* Folders */}
            {folders.length > 0 && (
              <div className="mb-6">
                <h2 className="text-sm font-medium text-muted-foreground mb-3">Folders</h2>
                <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 lg:grid-cols-6 gap-3">
                  {folders.map((folder) => (
                    <Card
                      key={folder.id}
                      className="group relative p-4 cursor-pointer hover:bg-accent/50 transition-colors"
                      onClick={() => setCurrentFolderId(folder.id)}
                    >
                      <div className="flex flex-col items-center gap-2">
                        <Folder className="w-10 h-10 text-primary" />
                        <span className="text-sm font-medium text-center truncate w-full">{folder.name}</span>
                      </div>
                      <Button
                        variant="ghost"
                        size="icon"
                        className="absolute top-1 right-1 h-6 w-6 opacity-0 group-hover:opacity-100 transition-opacity"
                        onClick={(e) => {
                          e.stopPropagation()
                          handleDeleteFolder(folder)
                        }}
                      >
                        <Trash2 className="w-3 h-3 text-destructive" />
                      </Button>
                    </Card>
                  ))}
                </div>
              </div>
            )}

            {/* Files */}
            {files.length > 0 && (
              <div>
                <h2 className="text-sm font-medium text-muted-foreground mb-3">Files</h2>
                <div className="space-y-2">
                  {files.map((file) => (
                    <Card
                      key={file.id}
                      className="group flex items-center gap-4 p-4 hover:bg-accent/30 transition-colors"
                    >
                      <div className="flex items-center justify-center w-10 h-10 rounded-lg bg-secondary text-lg">
                        {getFileIcon(file.type)}
                      </div>

                      <div className="flex-1 min-w-0">
                        <div className="flex items-center gap-2">
                          <File className="w-4 h-4 text-muted-foreground shrink-0" />
                          <span className="font-medium truncate">{file.originalName}</span>
                        </div>
                        <div className="flex items-center gap-3 text-xs text-muted-foreground mt-1">
                          <span>{formatBytes(file.size)}</span>
                          <span className="text-primary">{getCompressionRatio(file.size, file.compressedSize)}</span>
                          <span>{new Date(file.createdAt).toLocaleDateString()}</span>
                        </div>
                      </div>

                      <div className="flex items-center gap-1 opacity-0 group-hover:opacity-100 transition-opacity">
                        <Button variant="ghost" size="icon" className="h-8 w-8" onClick={() => handleDownload(file)}>
                          <Download className="w-4 h-4" />
                        </Button>
                        <Button
                          variant="ghost"
                          size="icon"
                          className="h-8 w-8 text-destructive hover:text-destructive"
                          onClick={() => handleDeleteFile(file)}
                        >
                          <Trash2 className="w-4 h-4" />
                        </Button>
                      </div>
                    </Card>
                  ))}
                </div>
              </div>
            )}

            {/* Empty State */}
            {folders.length === 0 && files.length === 0 && (
              <div className="text-center py-12">
                <div className="w-16 h-16 rounded-full bg-secondary flex items-center justify-center mx-auto mb-4">
                  <Folder className="w-8 h-8 text-muted-foreground" />
                </div>
                <h3 className="text-lg font-medium text-foreground mb-1">No files yet</h3>
                <p className="text-sm text-muted-foreground">Upload files or create folders to get started</p>
              </div>
            )}
          </>
        )}
      </main>

      {/* Footer */}
      <footer className="border-t border-border py-4 mt-auto">
        <div className="container mx-auto px-4">
          <p className="text-xs text-center text-muted-foreground">
            Access from any device on your local network ‚Ä¢ Files are compressed automatically
          </p>
        </div>
      </footer>
    </div>
  )
}
